
import Prism from 'prismjs';
import "prismjs/components/prism-sql"; 
import { useEffect } from 'react';


const MysqlComponent03 = () => {

  useEffect(() => {
    Prism.highlightAll();
  }, []);

  return (
    <div className="sql-container w-full">
      <pre>
        <code className="language-sql text-[4px] sm:text-[7px] md:text-[12px] lg:text-[15px]">
          {`
/* 
   (R: SELECT) 
   : SELECT [] FROM [];
*/
-- employee   id, ,  
SELECT emp_id, emp_name, email FROM employee;

-- employee     
SELECT * FROM employee;

-- department    
SELECT * FROM department;

-- employee  emp_id      
SELECT emp_id AS ' ' FROM employee;

-- employee    , (10%) 
SELECT salary, salary*0.1 AS bonus FROM employee;

--      
SELECT CURDATE() AS mdate ;
          `}
        </code>
      </pre>
    </div>
  )
}

export default MysqlComponent03