
import Prism from 'prismjs';
import "prismjs/components/prism-sql"; 
import { useEffect } from 'react';


const MysqlComponent13 = () => {

  useEffect(() => {
    Prism.highlightAll();
  }, []);

  return (
    <div className="sql-container w-full">
      <pre>
        <code className="language-sql text-[14px]">
          {`
/**
	    
    - (Join), (SubQuery)
*/
-- (Join)
-- 1. CROSS JOIN( : *) <--> CARTESIAN JOIN
-- 2. INNER JOIN() <--> EQUI-JOIN
-- 3. OUTER JOIN( +   ) <--> OUTER JOIN
-- 4. SELF JOIN  <--> SELF JOIN --->> Subquery   

--     ERD 
-- Database > Reverse Engineering ... >     next...

-- hrdb2019    
--  : SELECT [] 
-- 		FROM INFORMATION_SCHEMA.TABLES
--		WHERE TABLE_SCHEMA = ''
SELECT * FROM INFORMATION_SCHEMA.TABLES
	WHERE TABLE_SCHEMA ='HRDB2019';

-- 1. CROSS  :  *  * ...
--  : SELECT * FROM  JOIN ;
-- EMPLOYEE  DEPARTMENT  CROSS 
SELECT COUNT(*) FROM EMPLOYEE JOIN DEPARTMENT;  -- 20 * 7 ==> 140
SELECT * FROM EMPLOYEE CROSS JOIN DEPARTMENT;
SELECT * FROM DEPARTMENT JOIN EMPLOYEE;
SELECT * FROM DEPARTMENT;
SELECT * FROM EMPLOYEE;

-- DEPARTMENT  VACATION  
SELECT COUNT(*) FROM DEPARTMENT;  -- 7
SELECT COUNT(*) FROM VACATION;  -- 102
SELECT COUNT(*) FROM DEPARTMENT JOIN VACATION;   -- 714
SELECT COUNT(*) FROM DEPARTMENT, VACATION;  -- 714

-- DEPARTMENT  VACATION , EMPLOYEE 
SELECT COUNT(*) FROM DEPARTMENT AS D JOIN VACATION AS V JOIN EMPLOYEE AS E;   -- 14280
SELECT COUNT(*) FROM DEPARTMENT D, VACATION V, EMPLOYEE E;  -- Oracle, 14280

SELECT COUNT(*) 
FROM (SELECT * FROM DEPARTMENT JOIN VACATION) AS A
	  JOIN
      (SELECT * FROM DEPARTMENT JOIN VACATION) AS B ;  -- 509796

-- 2. INNER  :      
--  : SELECT * FROM 1 INNER JOIN 2
-- 		 ON 1.() = 2.();
-- () : SELECT * FROM 1, 2 WHERE 1. = 2.;

-- EMPLOYEE  DEPARTMENT  INNER  : DEPT_ID
SELECT * FROM EMPLOYEE INNER JOIN DEPARTMENT  -- 20
	ON EMPLOYEE.DEPT_ID = DEPARTMENT.DEPT_ID;

SELECT COUNT(*) FROM EMPLOYEE E, DEPARTMENT D  -- 20
	WHERE E.DEPT_ID = D.DEPT_ID;

-- , , , ,   
SELECT EMP_ID, EMP_NAME, D.DEPT_ID, DEPT_NAME, START_DATE
	FROM EMPLOYEE E INNER JOIN DEPARTMENT D
	ON E.DEPT_ID = D.DEPT_ID
    ORDER BY EMP_ID;

SELECT EMP_ID, EMP_NAME, D.DEPT_ID, DEPT_NAME, START_DATE
	FROM EMPLOYEE E, DEPARTMENT D
	WHERE E.DEPT_ID = D.DEPT_ID;
    -- ORDER BY EMP_ID;

--     id, ,  
SELECT D.DEPT_NAME, D.DEPT_ID, E.HIRE_DATE, E.SALARY
	FROM DEPARTMENT D INNER JOIN EMPLOYEE E
    ON D.DEPT_ID = E.DEPT_ID
    AND E.EMP_NAME = '' ;

--     , ,  
SELECT D.DEPT_ID, D.DEPT_NAME, E.EMP_NAME, E.HIRE_DATE, E.SALARY
	FROM EMPLOYEE E INNER JOIN DEPARTMENT D
    ON E.DEPT_ID = D.DEPT_ID
    AND D.DEPT_NAME = '';
    
--      
SELECT * 
	FROM EMPLOYEE E INNER JOIN DEPARTMENT D
    ON E.DEPT_ID = D.DEPT_ID
    AND D.DEPT_NAME = '';
    
--          
SELECT *
	FROM DEPARTMENT D INNER JOIN EMPLOYEE E INNER JOIN VACATION V
    ON D.DEPT_ID = E.DEPT_ID 
    AND E.EMP_ID = V.EMP_ID
    AND D.DEPT_NAME = '';

SELECT *
	FROM DEPARTMENT D, EMPLOYEE E, VACATION V
    WHERE D.DEPT_ID = E.DEPT_ID 
    AND E.EMP_ID = V.EMP_ID
    AND D.DEPT_NAME = '';    

--          ()

--    ''     , , ,   
--      
SELECT E.EMP_NAME, E.PHONE, D.DEPT_NAME, U.UNIT_NAME, V.REASON
	FROM EMPLOYEE E 
	INNER JOIN DEPARTMENT D 
    INNER JOIN VACATION V
    INNER JOIN UNIT U
    ON E.DEPT_ID = D.DEPT_ID
		AND E.EMP_ID = V.EMP_ID
        AND U.UNIT_ID = D.UNIT_ID
        AND V.REASON = ''
        AND D.DEPT_NAME = '';

--
SELECT E.EMP_NAME, E.PHONE, D.DEPT_NAME, U.UNIT_NAME, V.REASON
	FROM EMPLOYEE E	INNER JOIN DEPARTMENT D ON E.DEPT_ID = D.DEPT_ID
					INNER JOIN VACATION V ON E.EMP_ID = V.EMP_ID
					INNER JOIN UNIT U ON U.UNIT_ID = D.UNIT_ID
	WHERE V.REASON = ''  AND D.DEPT_NAME = '';


-- 2014 2015      
-- , , , ,  
SELECT E.EMP_ID, E.EMP_NAME, D.DEPT_NAME, E.HIRE_DATE, U.UNIT_NAME
	FROM EMPLOYEE E, DEPARTMENT D, UNIT U
    WHERE E.DEPT_ID = D.DEPT_ID
		AND D.UNIT_ID = U.UNIT_ID
		AND HIRE_DATE BETWEEN '2014-01-01' AND '2015-12-31' 
        AND RETIRE_DATE IS NULL;

SELECT E.EMP_ID, E.EMP_NAME, D.DEPT_NAME, E.HIRE_DATE, U.UNIT_NAME
	FROM EMPLOYEE E INNER JOIN DEPARTMENT D ON E.DEPT_ID = D.DEPT_ID
		INNER JOIN UNIT U ON D.UNIT_ID = U.UNIT_ID
    WHERE HIRE_DATE BETWEEN '2014-01-01' AND '2015-12-31' 
			AND RETIRE_DATE IS NULL;
            
-- OUTER JOIN : (INNER JOIN) + INNER JOIN                
--  : SELECT [] 
-- 			FROM 1  LEFT OUTER JOIN / RIGHT OUTER JOIN 2  ON 
-- *** OUTER JOIN      !!!

--       
SELECT DEPT_ID, DEPT_NAME, UNIT_NAME 
FROM DEPARTMENT D LEFT OUTER JOIN UNIT U ON D.UNIT_ID = U.UNIT_ID;

-- SELECT DEPT_ID, DEPT_NAME, UNIT_NAME 
-- FROM DEPARTMENT D, UNIT U 
-- WHERE D.UNIT_ID(+) = U.UNIT_ID;

-- 2017 2018   , , ,  
-- ,    
--   
SELECT 
	E.EMP_NAME, E.HIRE_DATE, E.SALARY, E.RETIRE_DATE, D.DEPT_NAME
    , D.DEPT_ID, U.UNIT_NAME
	FROM EMPLOYEE E INNER JOIN DEPARTMENT D ON E.DEPT_ID = D.DEPT_ID
					LEFT OUTER JOIN UNIT U ON D.UNIT_ID = U.UNIT_ID
    WHERE HIRE_DATE BETWEEN '2017-01-01'  AND '2018-12-31' ;

-- SELECT 
-- 	E.EMP_NAME, E.HIRE_DATE, E.SALARY, E.RETIRE_DATE, D.DEPT_NAME
--     , D.DEPT_ID, U.UNIT_NAME
-- 	FROM EMPLOYEE E, DEPARTMENT D, UNIT U
--     WHERE E.DEPT_ID = D.DEPT_ID
-- 		  AND D.UNIT_ID = U.UNIT_ID(+)
--           AND HIRE_DATE BETWEEN '2017-01-01'  AND '2018-12-31' ;       
          `}
        </code>
      </pre>
    </div>
  )
}

export default MysqlComponent13