
import Prism from 'prismjs';
import "prismjs/components/prism-sql"; 
import { useEffect } from 'react';


const MysqlComponent09 = () => {

  useEffect(() => {
    Prism.highlightAll();
  }, []);

  return (
    <div className="sql-container w-full">
      <pre>
        <code className="language-sql text-[14px]">
          {`
/**
	  :  (%, _) - LIKE   
     : WHERE [] LIKE ' + '
*/
--        
SELECT * FROM employee WHERE emp_name LIKE '%';

--  010     
SELECT * FROM employee WHERE phone LIKE '010%';

--    'a'   
SELECT * FROM employee WHERE email LIKE '_a%';

--   4  
SELECT * FROM employee WHERE email LIKE '____@%';

--  ''    
SELECT * FROM employee WHERE emp_name LIKE '%%';
          `}
        </code>
      </pre>
    </div>
  )
}

export default MysqlComponent09