
import Prism from 'prismjs';
import "prismjs/components/prism-sql"; 
import { useEffect } from 'react';


const MysqlComponent10 = () => {

  useEffect(() => {
    Prism.highlightAll();
  }, []);

  return (
    <div className="sql-container w-full">
      <pre>
        <code className="language-sql text-[14px]">
          {`
/**
	   : , , (), ...
     : , 
*/
-- 
--    : Dual
-- 123, -123   : ABS()
SELECT ABS(123), ABS(-123) FROM dual;
SELECT ABS(123), ABS(-123);

--   : FLOOR(), TRUNCATE()
SELECT FLOOR(123.456), FLOOR(3843.384349534);
SELECT TRUNCATE(123.456, 1), TRUNCATE(3843.384349534, 0);

--    : RAND()
SELECT FLOOR(RAND()*1000) AS num1, TRUNCATE(RAND()*1000, 2) AS num2;

--   : MOD()
SELECT MOD(20,2), MOD(21,2);
SELECT MOD(FLOOR(RAND()*1000),2) AS number1;

-- 
--   : CONCAT()
SELECT CONCAT('my','sq','l');

-- (S0001) --> emp_name
-- (S0001)  
SELECT CONCAT(emp_name,'(',emp_id,')') AS emp_name, hire_date, salary
 FROM employee
 WHERE CONCAT(emp_name,'(',emp_id,')') = '(S0001)';
 
--   : SUBSTRING(, , )
SELECT SUBSTRING('', 1,2);
SELECT SUBSTRING(' ', 6,3);

-- , , , ,  
SELECT emp_id, emp_name
		,SUBSTRING(hire_date,1,4) AS year
        ,SUBSTRING(hire_date,6,2) AS mm
        ,SUBSTRING(hire_date,9,2) AS dd
        , phone, email
 FROM employee;
 
 -- 2015   
 SELECT * FROM employee
 WHERE SUBSTRING(hire_date,1,4) = '2015';
 
 -- 1   
 SELECT * FROM employee
 WHERE SUBSTRING(hire_date,6,2) = '01';
 
 -- 2018     
 SELECT * FROM employee
 WHERE SUBSTRING(hire_date,1,4) = '2018' AND dept_id='SYS';
 
-- ,     : LEFT('',), RIGHT('',)
 SELECT LEFT('  !!!', 4), RIGHT('  !!!', 4);
 
-- 2018     
 SELECT * FROM employee
 WHERE LEFT(hire_date,4) = '2018' AND dept_id='SYS';
 
 -- ,  : UPPER(''), LOWER('')
 SELECT UPPER('abs'), LOWER('ABC');
 
 --      : SYS
 SELECT * FROM employee WHERE dept_id = UPPER('sys');
 
 --   : TRIM(' ')
SELECT TRIM('             ');
SELECT LTRIM('             ');
SELECT RTRIM('             ');
SELECT TRIM(LEADING '!' FROM '!!!!!!!!      ');
SELECT TRIM(TRAILING '!' FROM '!!!!!!!!');
 
 --  
 SELECT FORMAT(12345678, 0);
 
 -- , , ,  3  
SELECT emp_id, emp_name, CONCAT(LEFT(hire_date,4),'') AS hire_date,
		CONCAT(FORMAT(salary,0),'') AS salary
FROM employee;        

--   : CURDATE(), NOW()
SELECT CURDATE();  -- , , 
SELECT NOW();	   -- 2023-10-12 15:05:20  

SELECT NOW(), SLEEP(2), NOW(); -- 2023-10-12 15:08:33	0	2023-10-12 15:08:35
SELECT NOW(), SLEEP(2), NOW(); -- 2023-10-12 15:08:07	0	2023-10-12 15:08:07

--   : CAST(  AS )
-- 			, CONVERT(  AS ) 
SELECT CAST(20231012 AS CHAR); --  --> 
SELECT CAST(20231012 AS DATE); --  --> (DATE)
SELECT CAST('20231012' AS DATE); --  --> (DATE)

-- 123456789     3 
SELECT FORMAT(CAST(123456789 AS CHAR),0);
SELECT FORMAT(123456789, 0) ;

--   (,,)  ,  (DATETIME) 
SELECT CAST(CAST(CURDATE() AS CHAR) AS DATETIME);

--   (,,,,,)  ,  (DATETIME) 
SELECT CAST(CAST(NOW() AS CHAR) AS DATETIME);

-- employee      ?
SELECT emp_name, hire_date, CAST(hire_date AS DATETIME) FROM employee;

--  '20150101'    ,    
SELECT * FROM employee
WHERE hire_date = CAST('20150101' AS DATE);

SELECT * FROM employee
WHERE hire_date = '20150101';

DESC employee;
          `}
        </code>
      </pre>
    </div>
  )
}

export default MysqlComponent10