
import Prism from 'prismjs';
import "prismjs/components/prism-sql"; 
import { useEffect } from 'react';


const MysqlComponent12 = () => {

  useEffect(() => {
    Prism.highlightAll();
  }, []);

  return (
    <div className="sql-container w-full">
      <pre>
        <code className="language-sql text-[14px]">
          {`
/**
	  : UNION(DISTINCT), UNION ALL
     : SELECT 
		  UNION / UNION ALL
          SELECT 
    -      (  x)
    -      
*/
-- ,   , , ,  
SELECT emp_id, emp_name, dept_id, hire_date, CONCAT('id : ', dept_id) AS did FROM employee
WHERE dept_id = 'SYS'
UNION 
SELECT emp_id, emp_name, dept_id, hire_date, CONCAT('id : ', dept_id) AS did FROM employee
WHERE dept_id = 'MKT'
UNION
SELECT emp_id, emp_name, dept_id, hire_date, CONCAT('id : ', dept_id) AS did FROM employee
WHERE dept_id = 'SYS';

-- UNION ALL
SELECT emp_id, emp_name, dept_id, hire_date, CONCAT('id : ', dept_id) AS did FROM employee
WHERE dept_id = 'SYS'
UNION ALL
SELECT emp_id, emp_name, dept_id, hire_date, CONCAT('id : ', dept_id) AS did FROM employee
WHERE dept_id = 'MKT'
UNION ALL
SELECT emp_id, emp_name, dept_id, hire_date, CONCAT('id : ', dept_id) AS did FROM employee
WHERE dept_id = 'SYS';

--      
SELECT * FROM department
WHERE dept_id = (SELECT dept_id FROM employee WHERE emp_name ='');

SELECT dept_id FROM employee WHERE emp_name ='';   
          `}
        </code>
      </pre>
    </div>
  )
}

export default MysqlComponent12