
import Prism from 'prismjs';
import "prismjs/components/prism-sql"; 
import { useEffect } from 'react';


const MysqlComponent04 = () => {

  useEffect(() => {
    Prism.highlightAll();
  }, []);

  return (
    <div className="sql-container w-full">
      <pre>
        <code className="language-sql text-[14px]">
          {`
/* 
   (R: SELECT) 
     : SELECT [] 
      FROM []
            WHERE  = [];
*/
-- id S0007    
SELECT * FROM employee WHERE emp_id = 'S0007';

--      
SELECT * FROM employee WHERE emp_name = '';

--   , , , ,  
SELECT emp_id, gender, phone, email, salary
FROM employee
WHERE emp_name = '';

--      
SELECT * FROM department;
SELECT * FROM employee WHERE dept_id = 'GEN';

--   , , ,  
SELECT emp_name, hire_date, phone, salary
FROM employee
WHERE gender = 'M';

-- NULL   !! 
-- eng_name      
SELECT * FROM employee WHERE eng_name IS NULL;
SELECT * FROM employee;

-- eng_name NULL    
SELECT * FROM employee
  WHERE eng_name IS NOT NULL;
          `}
        </code>
      </pre>
    </div>
  )
}

export default MysqlComponent04