
import Prism from 'prismjs';
import "prismjs/components/prism-sql"; 
import { useEffect } from 'react';


const MysqlComponent05 = () => {

  useEffect(() => {
    Prism.highlightAll();
  }, []);

  return (
    <div className="sql-container w-full">
      <pre>
        <code className="language-sql text-[14px]">
          {`
/* 
  NULL     : IFNULL()
     : IFNULL(null , )
*/    
-- eng_name NULL ''   
SELECT IFNULL(eng_name, '') FROM employee;

-- retire_date NULL     
SELECT IFNULL(retire_date, CURDATE()) AS retire_date
FROM employee;
          `}
        </code>
      </pre>
    </div>
  )
}

export default MysqlComponent05